  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card:hover {
  transition-property: transform;
  transition-duration: .3s;
  transform: scale(1.2);
}

.card:not(:hover) {
  transition-duration: .3s;
  transform: scale(1);
}

.button_buy {
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  display: block;
}

.button_buy:hover {
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
  transition-duration: .3s;
}

.button_buy:not(:hover) {
  transition-duration: .3s;
}

.price {
